    onSaveInvoices = async () => {
        const _this = this;

        let initialDate = '';
        let finalDate = '';
        if (this.state.selectedOption === 'month') {
            const dateMonth =
                this.state.formData.month + '/' + this.state.formData.year;
            initialDate = moment(dateMonth, 'MM-YYYY').startOf('month');
            finalDate = moment(dateMonth, 'MM-YYYY').endOf('month');
        } else if (this.state.selectedOption === 'range') {
            initialDate = this.state.formData.periodoInicio;
            finalDate = this.state.formData.periodoFin;
        } else {
            console.log('alert');
        }

        if (initialDate !== '' && finalDate !== '') {
            const monthLabel = this.state.months.find(function(item) {
                return item.value === moment(initialDate).format('MM');
            });
            let invoiceData = {
                fecha_inicio: initialDate,
                fecha_fin: finalDate,
                subsidiariesId: this.state.formData.empresa,
                mes: monthLabel.label,
                conceptos: this.state.data,
                breakdown: this.state.resume
            };

            if (this.form.reportValidity()) {
                if (this.state.id == 'form') {
                    let invoiceResult = await feathers
                        .service('invoices')
                        .create(invoiceData);

                    if (invoiceResult._id != undefined) {
                        const jsonCFDI = {
                            InvoiceId: invoiceResult._id
                        };
                        let cfdiResult = await feathers
                            .service('cfdi')
                            .create(jsonCFDI);

                        window.location.href = '/#/facturacion';
                    }
                } else {
                    let invoiceResult = await feathers
                        .service('invoices')
                        .patch(_this.state.id, _this.state.formData);
                    if (invoiceResult._id != undefined) {
                        window.location.href = '/#/facturacion';
                    }
                }
            }
        }
    };

